.formWrapper {
  width: 413px;
  display: flex;
  flex-direction: column;
  margin: 60px auto;
  background-color: #fff;
  -webkit-box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.2);
  place-content: center;
}

.formContainer {
  margin: 34px 0 38px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.heading {
  font-weight: 600;
  font-size: 26px;
  line-height: 32px;
}

.form {
  margin: 40px 34px;
  display: flex;
  flex-direction: column;
  place-content: center;
  position: relative;
}

.label {
  display: inline-block;
  margin-top: 24px;
  font-weight: 400;
  font-size: 13px;
  line-height: 20px;
  opacity: 0.5;
}

.instructions {
  padding: 26px 0 64px;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  opacity: 0.5;
}

.button {
  width: 347px;
  height: 36px;
}

.account {
  font-weight: 600;
  font-size: 14px;
  line-height: 24px;
  color: #000;
  opacity: 0.5;
  margin-bottom: 7px;
}

.formContainer a {
  color: var(--primaryColor);
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
}